<template>
    <div class="content-wrapper" id="upload">
        <div class="text-center" style="margin-top: 20px" id="container">
            <a href="javascript:void(0);" type="button" id='pickfiles'>
                <i class="fa fa-plus-square fa-5x" aria-hidden="true"
                   style="color: steelblue;vertical-align: middle"></i>
            </a>
            <h5 style="margin-top: 10px"></h5>
        </div>
        <div v-if="progress!=100&&!success">
            <div class="row" style="margin-top: 10px">
                <div
                        class="col-md-8 col-lg-8 col-sm-8 col-xs-8 col-xs-offset-2 col-sm-offset-2 col-md-offset-2 col-lg-offset-2">
                    <h4 v-if="file!={}&&file!=null&&!success">{{file.name}}<code style="margin-left:10px">{{file.size}}</code>
                    </h4>

                    <div class="progress progress-xs" v-show="uploading||(progress>0&&progress<100)&&!success">
                        <div class="progress-bar progress-bar-danger progress-bar-striped" role="progressbar"
                             aria-valuenow="60"
                             aria-valuemin="0" aria-valuemax="100" :style="{width: progress+'%'}">
                        </div>
                    </div>

                </div>
            </div>
            <div
                    class="input-group col-md-8 col-lg-8 col-sm-8 col-xs-8 col-xs-offset-2 col-sm-offset-2 col-md-offset-2 col-lg-offset-2"
                    v-if="file!={}&&file!=null&&!success" style="margin-top:30px">
                <div class="input-group-addon">
                    <i class="fa fa-envelope-o"></i>
                </div>
                <input type="text" class="form-control" v-model='remark' placeholder="">
            </div>
            <div v-if="file!={}&&file!=null&&!success" style="margin-top:30px;text-align: center">
                <button
                        class="btn btn-success" :class="{disabled:loading}" style="width: 250px"
                        @click="upload">{{uploadText}}
                </button>
            </div>
        </div>
        <div v-if="success" class="text-center" style="margin-top:30px">
            <h4>,</h4>
            <qrcode :value="sourceLink" size=200></qrcode>
            <strong>()</strong>
        </div>
        <Tips :msg.sync="message" title="" :show.sync="showTips" :ok-text="okText" @ok="dismiss" @cancel="dismiss">
        </Tips>

    </div>
</template>
<style>
    body {
        background-color: #ffffff;
    }
</style>
<script>
    import Qiniu from 'Qiniu'
    import Tips from './Tips.vue'
    //https://github.com/atwxp/v-qrcode
    import qrcode from 'v-qrcode'
    export default {
        components: {Tips, qrcode},
        data() {
            return {
                uploader: {},
                file: null,
                success: false,
                progress: 0,
                remark: '',
                uploading: false,
                loading: false,
                showTips: false,
                isFull: false,
                uploadText: '',
                message: '',
                okText: '',
                sourceLink: '',
                //
                uoloadedfile: {
                    //id
                    _id: '',
                    //
                    fileType: '',
                    //key
                    key: '',
                    //fileId
                    fileId: '',
                    //userID
                    userID: ''
                }
            }
        },
        computed: {},
        mounted: function () {
            this.$nextTick(function () {
                return this.init();
            })
        },
        methods: {
            dismiss: function () {
                this.showTips = false;
            },
            init: function () {
                var that = this;
                console.log(that.$store.state.baseUrl + '/qiniu/upload/token');
                this.uploader = Qiniu.uploader({
                    runtimes: 'html5,flash,html4',      // 
                    browse_button: 'pickfiles',         // 
//          uptoken: 'kZ_fJrAQFy5sGrUThoJ8sXMx1yz_Uu40ehqyKOuT:pSPIuUutXRr2tLIpKn4ARRZy-Zs=:eyJkZWFkbGluZSI6MTgwNDA0NzgzNCwic2NvcGUiOiJjb21tb25yZXNvdXJjZSJ9',
                    uptoken_url: that.$store.state.baseUrl + '/qiniu/upload/token',         // AjaxuptokenUrl
                    uptoken_func: function (file) {    // uptoken
                        // do something
                        return uptoken;
                    },
                    get_new_uptoken: true,             // uptoken
                    // downtoken_url: '/downtoken',
                    // AjaxdownTokenUrlJS-SDKPOSTkeydomainJSONurlurl
                    unique_names: true,              // falsekeyJS-SDKkey
                    // save_key: true,                  // falseuptokensava_keySDKkey
                    domain: 'http://omt3mqb30.bkt.clouddn.com/',     // bucket
                    container: 'container',             // DOM IDbrowser_button
                    max_file_size: '1024mb',             // 
                    max_retries: 3,                     // 
                    dragdrop: false,                     // 
                    chunk_size: '0mb',                  // 
                    multi_selection: false,
                    auto_start: false,                  // 
                    filters: {
                        max_file_size: '1024mb',
                        mime_types: [ //office
                            {title: "Image files", extensions: "jpg,gif,png,jpeg,doc,pdf,docx,pptx,ppt,xls,xlsx"},
                            {title: "office file", extensions: 'doc,pdf,docx,pptx,ppt,xls,xlsx'}
                        ],
                        prevent_duplicates: true //
                    },
                    x_vars: {
                        'userid': function () {
                            return '58de53c121e7dc3aa8e48cb8';
                        },
                        'note': function () {
                            return that.remark;
                        }
                    },
                    init: {
                        'FilesAdded': function (up, files) {
                            that.success = false;
                            that.file = files[0];
                            that.progress = 0;
                            that.uploading = false;
                            that.uploadText = '';
                        },
                        'UploadProgress': function (up, file) {
                            console.log('UploadProgress', file);
                            that.progress = file.percent;
                        },
                        'Error': function (up, err) {
                            console.log('Error', err);
                            that.message = err.message;
                            that.showTips = true;
                        },
                        'FileUploaded': function (up, file, info) {

                            let result = JSON.parse(info);
                            console.log("result", result);
                            if (!result._id) {
                                that.showTips = true;
                                that.message = result.error ? result.error : '';
                                return;
                            }

                            // 
                            that.uoloadedfile._id = result._id;
                            that.uoloadedfile.fileType = result.filename.substring(result.filename.lastIndexOf(".") + 1, result.filename.length);
                            that.uoloadedfile.key = result.key;
                            //pdfimage,
                            if (that.uoloadedfile.fileType.indexOf('png') > -1
                                || that.uoloadedfile.fileType.indexOf('jpg') > -1
                                || that.uoloadedfile.fileType.indexOf('gif') > -1
                                || that.uoloadedfile.fileType.indexOf('pdf') > -1) {
                                that.showSuccess();
                                return;
                            }
                            //
                            that.transform(that.uoloadedfile._id, that.uoloadedfile.fileType);
                        },
                    }
                });
            },
            upload: function () {
                if (!this.uploading) {
                    this.uploading = true;
                    this.uploader.start();
                    this.uploadText = '';
                }
                else {
                    this.uploading = false;
                    this.uploader.stop();
                    this.uploadText = '';
                }
            },
            transform: function (id, type) {
                $.ajax({
                    type: 'PUT',
                    url: this.$store.state.baseUrl + '/qiniu/persistent/file',
                    data: {
                        id: id,
                        type: type
                    },
                    success: this.transformResult,
                    error: this.transformResult
                })
            },
            getResult: function (id) {
                $.ajax({
                    type: 'GET',
                    url: this.$store.state.baseUrl + '/qiniu/persistent/file',
                    data: {
                        id: id,
                    },
                    success: this.result,
                    error: this.result
                })
            },


            transformResult: function (data) {
                if (data && data.error) {
                    this.showTips = true;
                    this.message = data.error ? data.error : ',';
                    return;
                }
                else {
                    this.getResult(this.uoloadedfile._id);
                }

            },
            result: function (data) {
                if (!data || data.code === 1 || data.code === 2) {
                    this.getResult(this.uoloadedfile._id);
                }
                else if (data.code === 3) {
                    this.showTips = true;
                    this.message = data.error ? data.error : ',';
                }
                else {
                    this.showSuccess();
                }
            },
            showSuccess: function () {
                this.success = true;
                this.sourceLink = this.$store.state.domain + this.uoloadedfile.key;
                this.progress = 100;
                this.uploading = false;
                this.uploadText = '';
            },
        }

    }
</script>



